import gql from "graphql-tag";
export default {
  apollo: {
    movie: gql`
      query MyQuery {
  movie(order_by: {Release_Date: desc}) {
    poster_path
    id
    Release_Date
    Rating
    Name
    Description
    Author
    video_path
    genre_id
  }
}

    `,

    genre: gql`
    query MyQuery {
  genre {
    id
    genre_name
  }
}
`,



    },

  

  methods: {
    redPage(idM) {
      this.$router.push({name: 'movies-streaming-id' , params: {id: idM}})
    }
  }
};
